.btn {
  height: 40px;
  padding: 0 20px;
  border-radius: 10px;
  background: $black-3;
  @include font(18px, $white);
  &-white {
    background: $white;
    border: 1px solid $bg-c;
    color: $black-3;
  }
  @media all and (max-width: 767px) {
    height: 30px;
    @include font(15px);
  }
}

.btn-bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 0;
  margin-top: 30px;
  border-top: 1px solid $black-8;
  .lft-set {
    display: flex;
    align-items: center;
    gap: 0 10px;
  }
  .rgt-set {
    display: flex;
    align-items: center;
    gap: 0 10px;
  }
  @media all and (max-width: 767px) {
  }
}
