.weather-box {
  position: relative;
  width: 500px;
  padding: 20px;
  margin: 50px auto 0;
  border: 1px solid #ccc;
  border-radius: 10px;
  text-align: center;
  & > * {
    @include font(20px);
    line-height: 1.6;
  }
}

.App {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.btn-set {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
  .btn {
    &.active {
      background: gray;
    }
  }
}

.spinner {
  @include center(both);
  left: 48.5%;
  top: 46.5%;
}

@media all and (max-width: 767px) {
  .App {
    height: 80vh;
  }

  .weather-box {
    width: 80%;
    margin: 0 auto;
    & > * {
      @include font(16px);
    }
  }

  .btn-set {
    width: 80%;
    flex-wrap: wrap;
    justify-content: center;
    gap: 5px;
    margin: 20px auto 0;
  }

  .spinner {
    @include center(both);
    left: 42.5%;
    top: 36.5%;
  }
}
